import React from 'react';
import PropTypes from 'prop-types';
import makeClass from 'classnames';
import react from '@servicenow/ui-renderer-react';

import {
	hasProp,
	controlledFieldPropTypes,
	checkAutofocus
} from '@devsnc/sn-controls-common';

import style from './link.scss';

import { isFunction } from 'lodash';

const { createSlot } = react;

/**
 * Renders a hyperlink
 */
class SNLink extends React.Component {
	static componentName = 'sn-record-link';
	static style = style;
	static propTypes = {
		...controlledFieldPropTypes,

		/**
		 * [URL that is linked to](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/a#attr-href)
		 */
		href: PropTypes.string,

		/**
		 * [Where to display URL](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/a#attr-target)
		 */
		target: PropTypes.string,

		/**
		 * [Relationship of target object](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/a#attr-rel)
		 */
		rel: PropTypes.string,

		/**
		 * ARIA role
		 */
		role: PropTypes.string,

		/**
		 * Size of the link
		 */
		size: PropTypes.oneOf(['sm', 'xs']),

		/**
		 * Style variant
		 */
		variant: PropTypes.oneOf(['dark']),

		/**
		 * Optional accessibility parameter
		 */
		ariaLabel: PropTypes.string,

		/**
		 * Optional accessibility parameter
		 */
		ariaDescribedby: PropTypes.string,

		/**
		 * Optional accessibility parameter
		 */
		ariaLabelledby: PropTypes.string,

		/**
		 * @type {{'aria-*': string}}
		 */
		configAria: PropTypes.object
	};

	onClick = event => {
		if (isFunction(this.props.onClick)) {
			this.props.onClick(event);
		}
	};

	onFocus = event => {
		if (isFunction(this.props.onFocus)) {
			this.props.onFocus(event);
		}
	};

	handleRef = node => {
		this.anchor = node;

		if (isFunction(this.props.innerRef)) {
			this.props.innerRef(node);
		}
	};

	componentDidMount() {
		checkAutofocus(this.props.autofocus, this.anchor);
	}

	render() {
		const className = makeClass('sn-record-link', {
			[`-${this.props.variant}`]: this.props.variant,
			[`-${this.props.size}`]: this.props.size
		});

		const ariaDescribedby =
			this.props.configAria && this.props.configAria['aria-describedby']
				? this.props.configAria['aria-describedby']
				: this.props.ariaDescribedby;

		const ariaLabel =
			this.props.configAria && this.props.configAria['aria-label']
				? this.props.configAria['aria-label']
				: this.props.ariaLabel;

		const ariaLabelledby =
			this.props.configAria && this.props.configAria['aria-labelledby']
				? this.props.configAria['aria-labelledby']
				: this.props.ariaLabelledby;

		return (
			<a
				href={this.props.href}
				target={this.props.target}
				rel={this.props.rel}
				role={this.props.role}
				id={this.props.componentId}
				slot={this.props.slot}
				className={className}
				ref={this.handleRef}
				onClick={this.onClick}
				onMouseEnter={this.props.onMouseEnter}
				onMouseLeave={this.props.onMouseLeave}
				onFocus={this.onFocus}
				onBlur={this.props.onBlur}
				onKeyDown={this.props.onKeyDown}
				onKeyUp={this.props.onKeyUp}
				onKeyPress={this.props.onKeyPress}
				aria-describedby={ariaDescribedby}
				aria-label={ariaLabel}
				aria-labelledby={ariaLabelledby}
				aria-disabled={hasProp(this.props.readonly)}
			>
				<span>{createSlot(this.props.children)}</span>
			</a>
		);
	}
}

export default SNLink;
